import React, { useState, useEffect } from 'react';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';

// 
interface Task {
  id: number;
  date: string;
  task: string;
  status: '' | '';
  className: string;
}

const CalendarComponent: React.FC = () => {
  const [tasks, setTasks] = useState<Task[]>([]); // 
  const [workdays, setWorkdays] = useState<string[]>([]); // 
  const [newTask, setNewTask] = useState<string>(''); // 
  const [selectedDate, setSelectedDate] = useState<Date | null>(null); // 

  // 
  useEffect(() => {
    const savedTasks = JSON.parse(localStorage.getItem('tasks') || '[]') as Task[];
    const savedWorkdays = JSON.parse(localStorage.getItem('workdays') || '[]') as string[];
    setTasks(savedTasks);
    setWorkdays(savedWorkdays);
  }, []); // 

  // 
  useEffect(() => {
    localStorage.setItem('tasks', JSON.stringify(tasks));
    localStorage.setItem('workdays', JSON.stringify(workdays));
  }, [tasks, workdays]); //  tasks  workdays 


  // 
  const toggleTaskStatus = (taskId: number) => {
    setTasks(
      tasks.map((task) =>
        task.id === taskId
          ? {
            ...task,
            status: task.status === '' ? '' : '',
            className: task.status === '' ? 'button-completed' : 'button-pending',
          }
          : task
      )
    );
  };

  // 
  const handleWorkdaySelection = (date: Date) => {
    const dateString = date.toDateString();
    if (workdays.includes(dateString)) {
      setWorkdays(workdays.filter((day) => day !== dateString));
    } else {
      setWorkdays([...workdays, dateString]);
    }
  };

  // 
  const handleTaskInput = (e: React.ChangeEvent<HTMLInputElement>) => {
    setNewTask(e.target.value);
  };

  // 
  const addNewTask = () => {
    if (selectedDate) {
      const newTaskObj: Task = {
        id: new Date().getTime(), //  id
        date: selectedDate.toDateString(),
        task: newTask,
        status: '',
        className: 'button-pending',
      };
      setTasks([...tasks, newTaskObj]);
      setNewTask(''); // 
    }
  };

  // 
  const renderTasksForSelectedDate = (): Task[] => {
    if (!selectedDate) return [];
    return tasks.filter((task) => task.date === selectedDate.toDateString());
  };

  return (
    <div className="calendar-container content-section">
      <h2 className="major"></h2>
      <div className="instructions">
        <p></p>
        <ol>
          <li></li>
          <li></li>
          <li></li>
        </ol>
      </div>

      {/*  */}
      <div className="workday">
        <h3></h3>
        <Calendar
          onClickDay={(date) => {
            setSelectedDate(date); // 
            handleWorkdaySelection(date); // 
          }}
          tileClassName={({ date }) => {
            const isWorkday = workdays.includes(date.toDateString());
            const isSelected = selectedDate && selectedDate.toDateString() === date.toDateString();
            return isSelected
              ? 'selected-day'   // 
              : isWorkday
                ? 'workday'        // 
                : '';              // 
          }}
        />
      </div>

      {/*  */}
      <div className="todo-section">
        <h3></h3>
        <div className="form-group">
          <input
            type="text"
            className="form-control"
            value={newTask}
            onChange={handleTaskInput}
            placeholder=""
          />
          <button
            className="button-primary"
            onClick={addNewTask}
            disabled={!selectedDate || !newTask} // 
          >
            
          </button>
        </div>
      </div>

      {/*  */}
      <div className="table-wrapper">
        <table className="table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {renderTasksForSelectedDate().map((task) => (
              <tr key={task.id}>
                <td>{task.date}</td>
                <td>{task.task}</td>
                <td>
                  <button
                    className={task.className}
                    onClick={() => toggleTaskStatus(task.id)} //  task.id 
                  >
                    {task.status}
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default CalendarComponent;
